.about-text h1 {
    margin: 3px;
}

.about-text p {
    font-size: 21px;
    font-weight: 300;
    max-width: 500px;
    width: 100%;
    margin-top: 10px;
}

@media (max-width: 768px) {
    .about-text p {
        font-size: 18px;
    }
}

.t1,
.t2 {
    transform: translateY(-150px);
}

.t3 {
    transform: translateY(150px);
}

.hl {
    position: relative;
}

.hl::after {
    position: absolute;
    width: 100%;
    content: '';
    height: 3px;
    border-radius: 5px;
    background: var(--red);
    bottom: -2px;
    left: 0;
    transform-origin: center;
    transform: scaleX(0);
}