:root {
    --dark: #161616;
    --black: #182538;
    --gray: #a4a4a5;
    --white: #ebebeb;
    --green: #A0D468;
    --red: #CC284D;
    --blue: #3BAFD9;
    --orange: #FB6E52;
    --green: #A0D468;
    --violet: #744498;
    --yellow: #f4b31e;
    --light-gray: #ebebeb;
}

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    outline: none !important;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
    font-family: 'Poppins', sans-serif;
    color: var(--black);
    font-weight: 300;
}

a,
a:link,
a:visited {
    text-decoration: none;
    cursor: pointer;
    color: var(--primary);
}

a:hover {
    color: var(--primary);
}

h1 {
    font-weight: 700;
    letter-spacing: -2px;
    font-size: 55px;
    line-height: 60px;
}

@media (min-width: 769px) and (max-width: 900px) {
    h1 {
        font-size: 45px;
        line-height: 50px;
    }
}

@media (max-width: 768px) {
    h1 {
        font-size: 30px;
        line-height: 35px;
    }
}

h2 {
    letter-spacing: -2px;
    font-size: 30px;
}

@media (min-width: 769px) and (max-width: 900px) {
    h2 {
        font-size: 25px;
    }
}

@media (max-width: 768px) {
    h2 {
        font-size: 20px;
    }
}

p {
    letter-spacing: -.5px;
}

img {
    max-width: 100%;
    height: auto;
}

button {
    cursor: pointer;
    font: inherit;
    border: none;
    background: transparent;
}

ul {
    list-style: none;
}

section {
    min-height: 100vh;
    width: 100%;
}


/* Layout */

.container {
    max-width: 1000px;
    width: 100%;
    margin: 0 auto;
}

.flex {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}

.spacer {
    flex-grow: 1;
}

.center {
    align-items: center;
    justify-content: center;
}

.h-center {
    justify-content: center;
}

.v-center {
    align-items: center;
}

.flex-column {
    flex-direction: column;
}

.overflow-hidden {
    overflow: hidden;
}

.position-relative {
    position: relative;
}

.position-absolute {
    position: absolute;
}

.absolute-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.w-100 {
    width: 100%;
}

.h-100 {
    height: 100%;
}

.vh-100 {
    height: 100vh;
}

.p15 {
    padding: 15px;
}

.p20 {
    padding: 20px;
}

.text-center {
    text-align: center;
}


/* Layout */

body::-webkit-scrollbar {
    width: 7px;
}

body::-webkit-scrollbar-track {
    background: var(--gray);
}

body::-webkit-scrollbar-thumb {
    background: var(--dark);
}

body::-webkit-scrollbar-thumb:hover {
    background: var(--dark);
}