.header {
    position: absolute;
    top: 0;
}

.contact-links,
.logo {
    transform: translateY(-100px);
}

.logo {
    font-size: 29px;
    font-weight: 700;
    line-height: 29px;
    cursor: default;
    transition: all .3s;
    user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
    letter-spacing: -2px;
}

.dot {
    display: inline-block;
    color: var(--red);
}


/* Contact Links */

.contact-links {
    transition: all .3s;
}

.contact-links a {
    line-height: 10px;
}

.contact-links a:first-child {
    margin-right: 20px;
}

.contact-links svg {
    fill: var(--black);
    transition: all .3s;
}

.contact-links svg:hover {
    fill: var(--red);
}