.contact {
    width: 100%;
    height: 100vh;
    padding: 15px;
    overflow: hidden;
    background: var(--black);
    color: var(--white);
}

.contact-title,
.contact-form {
    flex: 1;
}

@media (max-width: 768px) {
    .contact {
        flex-direction: column;
    }
    .contact-form {
        flex: 1.2;
        align-items: flex-start;
    }
    .contact-animation {
        flex: .8;
    }
}

@media(min-width:768px) {
    .contact-info {
        margin-top: -60px;
    }
}

.contact-info>* {
    margin-top: 10px;
}

.contact-info a {
    transition: all .3s;
    line-height: 10px;
}

.contact-info a:first-child {
    margin-right: 10px;
}

.contact-info svg {
    fill: var(--white);
    transition: all .3s;
}

.contact-info svg:hover {
    fill: var(--red);
}

#contact-form, .contact-info {
    max-width: 400px;
    width: 100%;
}

input,
textarea {
    width: 100%;
    padding: 8px 0;
    margin-bottom: 40px;
    border: none;
    background: transparent;
    border-bottom: .5px solid #9e9e9e;
    color: var(--white);
    font-family: inherit;
    font-size: inherit;
    -webkit-appearance: none;
    transition: all .3s;
    border-radius: 0;
    color: var(--white) !important;
}

textarea {
    resize: none;
    margin-top: 5px;
}


/* Floating Labels */

.label {
    position: absolute;
    top: 10px;
    left: 0;
    font-size: 14px;
    transition: all 0.2s ease;
}

.input.active+.label {
    transform: translate(-5px, -28px) scale(.8);
}

.btn {
    background: var(--red);
    color: var(--white) !important;
    line-height: 14px;
    width: 100px;
    height: 40px;
    padding: 7px 20px;
}

.btn:focus,
.btn:active {
    outline: none !important;
    box-shadow: none;
}


/* sending animation */

.paper-plane {
    width: 20px;
}

.btn-text,
.success,
.failure,
.paper-plane {
    position: absolute;
}

.paper-plane {
    transition: translateX .5s;
    transform: translateX(-150px);
}

.success,
.failure {
    opacity: 0;
    transform: scale(0);
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    box-shadow: 0 0 0 30px var(--black) inset !important;
    -webkit-box-shadow: 0 0 0 30px var(--black) inset !important;
    -webkit-text-fill-color: var(--white) !important;
}

input:-webkit-autofill+.label,
input:-webkit-autofill:hover+.label,
input:-webkit-autofill:focus+.label,
input:-webkit-autofill:active+.label {
    transform: translate(-5px, -28px) scale(.8);
}