.projects {
    background: var(--red);
    color: var(--white);
}

.projects-cards {
    gap: 20px;
    flex-wrap:wrap;
}

.project {
    background: rgba(0, 0, 0, .3);
    position: relative;
    width: 31%;
    height: 35vh;
    overflow: hidden;
}

@media (min-width: 501px) and (max-width: 800px) {
    .project {
        width: 48%;
    }
}

@media (max-width: 500px) {
    .project {
        width: 100%;
    }
}

.project-inner {
    position: absolute;
    z-index: 1;
    left:0;
    top:0;
    background: var(--white);
    padding: 20px;
    width: 100%;
    height: 100%;
    transition: all .5s ease-in-out;
    transform: translateY(90%);
}

.project-inner-btn {
    position:absolute;
    top: 2px;
    cursor: pointer;
}

.project-inner:hover {
    transform: translateY(0);
}

/* project links */

.project-links {
    gap: 20px;
}

.project-link {
    color: var(--white) !important;
    background: var(--black);
    padding: 7px 20px;
}

/* Tags */

.tag {
    color: var(--white);
    background: var(--black);
    font-size: 12px;
    font-weight: 300;
    padding: 3px 7px;
    border-radius: 5px;
    margin-top: 10px;
}

.tag:not(:last-child) {
    margin-right: 5px;
}